import React from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

const CookieModal = (props) => {
    return (
        <Modal show={! props.acceptedCookies} onHide={() => console.log("HIDDEN")} animation={false}>
            <Modal.Header>
                <Modal.Title>Cookies</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                This site uses <a href="/cookies/" target="_blank">cookies</a> to work.
            </Modal.Body>
            <Modal.Footer>
                <Button variant="success" onClick={props.acceptClickHandler}>
                    Accept Cookies
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default CookieModal;